
.user-wrap {
  overflow-y: scroll;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  top: 40px;
  bottom: 40px;
}
.user-wrap .user-info-wrap {
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
  background-color: #4FE4FF;
}
.user-wrap .user-info-wrap .big-avatar {
  border-radius: 50%;
  margin: 0 auto;
  background-color: #fff;
}
.user-wrap .user-topiclist-wrap .topic-item {
  border-bottom: 1px solid #eee;
  padding: 10px 20px;
}
.user-wrap .user-topiclist-wrap .topic-item .avatar {
  float: left;
}
.user-wrap .user-topiclist-wrap .topic-item .topic-info {
  padding-left: 20px;
  float: left;
  color: #222;
}
.user-wrap .user-topiclist-wrap .topic-item .topic-info .topic-title {
  font-size: 16px;
  color: #000;
  font-weight: 600;
}
.user-wrap .user-topiclist-wrap .topic-item::after,
.user-wrap .user-topiclist-wrap .topic-item::before {
  content: '';
  display: table;
  clear: both;
}



