/** common */
.avatar {
  display: inline-block;
  width: 40px;
  height: 40px;
}
.avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
/**主要内容**/
.wrap {
  overflow-y: scroll;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  top: 50px;
  bottom: 40px;
  /** 顶部帖子信息 **/
  /**内容区**/
  /**回复区**/
}
.wrap .topic-intro .head-title {
  text-align: center;
}
.wrap .topic-intro .collect-topic {
  float: right;
  margin-right: 30px;
  background-color: #66FA76;
}
.wrap .topic-intro .collect-topic:hover {
  cursor: pointer;
}
.wrap .topic-intro .collect-topic .collect {
  background-color: #66FA76;
  width: 60px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #fff;
}
.wrap .topic-intro .collect-topic .iscollect {
  background-color: #ccc;
}
.wrap .topic-intro .head-info:after,
.wrap .topic-intro .head-info:before {
  content: '';
  display: table;
  clear: both;
}
.wrap .topic-intro .head-info .avatar {
  float: left;
}
.wrap .topic-intro .head-info .info-detail {
  float: left;
  margin-left: 10px;
}
.wrap .content {
  box-shadow: 0 6px 4px #eee;
  padding: 6px 16px;
}
.wrap .comment-wrap {
  margin-top: 10px;
}
.wrap .comment-wrap .comment-count {
  padding: 20px 0;
}
.wrap .comment-wrap .comment-item {
  border-top: 1px solid #eee;
  padding: 18px;
}



