
.message-wrap {
  overflow-y: scroll;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  top: 40px;
  bottom: 60px;
}
.message-wrap .message-list .topic-item {
  border-bottom: 1px solid #eee;
  padding: 10px 20px;
}
.message-wrap .message-list .topic-item .avatar {
  float: left;
}
.message-wrap .message-list .topic-item .avatar img {
  width: 40px;
  height: 40px;
}
.message-wrap .message-list .topic-item .topic-info {
  padding-left: 20px;
  float: left;
  color: #222;
}
.message-wrap .message-list .topic-item .topic-info .topic-title {
  font-size: 16px;
  color: #000;
  font-weight: 600;
}
.message-wrap .message-list .topic-item::after,
.message-wrap .message-list .topic-item::before {
  content: '';
  display: table;
  clear: both;
}



