
.topic-list {
  /**导航**/
  /**话题列表**/
}
.topic-list .nav-bar {
  z-index: 2;
}
.topic-list .scroller-wrap {
  padding-top: 90px;
  box-sizing: border-box;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  color: #222;
}
.topic-list .topic-wrap .topic-item {
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}
.topic-list .topic-wrap .topic-item .topic-title {
  font-size: 16px;
  color: #000;
  font-weight: 600;
}
.topic-list .topic-wrap .topic-item .topic-content {
  color: #222;
  font-size: 14px;
}
.topic-list .topic-wrap .topic-item .topic-content .topic-info-left {
  float: left;
}
.topic-list .topic-wrap .topic-item .topic-content .topic-info-left .avatar {
  float: left;
  width: 40px;
  height: 40px;
  display: inline-block;
}
.topic-list .topic-wrap .topic-item .topic-content .topic-info-left .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.topic-list .topic-wrap .topic-item .topic-content .topic-info-left .author-info {
  float: left;
  padding-left: 6px;
}
.topic-list .topic-wrap .topic-item .topic-content .topic-info-right {
  float: right;
}
.topic-list .topic-wrap .topic-item .topic-content::after,
.topic-list .topic-wrap .topic-item .topic-content::before {
  content: '';
  display: table;
  clear: both;
}



